import { Component, OnInit, OnChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../../http/product-manage.http.service";
import * as _ from 'lodash';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config'//中文
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TreeNode } from 'primeng/primeng';

@Component({
  selector: 'app-channel-delivery-details',
  templateUrl: './channel-delivery-details.component.html',
  styleUrls: ['./channel-delivery-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 产品投放
 */
export class ChannelDeliveryDetailsComponent implements OnInit, OnChanges {
  categoryIdA: any; // 当前目录ID
  toufanglistA: any = []; // 获取该分类下所有数据（用于状态盼盼保存）
  isSiteClick: boolean = true;
  issiteFClick: boolean = false;
  isNB: boolean = false;
  deliveryType: any; // 是否立即投放
  code: any;
  deliveryTypeV: string = ''; // 是否立即投放Value
  startTime: Date = null;
  transferedDateNow: Date = null;
  dataV: boolean = true;
  fenlei_id: string = '-1'; // 分类
  fenlei_obj: any; // 分类下存值
  siteId: string; // 站点ID
  siteIdList: any = []; // 站点ID数组
  siteNameList: any = []; // 站点Name数组
  showClass: string; // 点击不同产品
  showClassSite: string; // 点击不同站点
  homeDirectoryID: string; // 当选站点下主目录
  //   mainMap={}
  ch: any = CALENDAR_CN;
  selectedFiles: TreeNode[];
  keshou_obj2: any; // fuzhi
  biaoq_obj2: any; // fuzhi

  // 模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;
  // 提示信息
  msgs: Message[] = [];
  channelId: string; // 渠道id
  channelName: string; // 站点名称
  productStoreId: string; // 店铺标识
  storeName: string; // 店铺名称
  muluName: any; // 目录
  keshou_list: any; // 可售列表
  gongneng_list: any = []; // 功能列表
  xuanze: any = []; // 功能选择
  gongneng_value: any;

  label_list: any = []; // 标签列表
  bqxuanze: any = []; // 标签选择
  //   label_value: any;

  // 组件的参数
  updateValue: any = [];

  // 树结构
  treeData: any;
  left_tree: any = [];
  left_tree_arr: any = [];
  all_tree; any = [];
  hierarchy_arr: any = [];
  ctlgIdValue: any;

  productCheckbox: any = [];
  // 产品回显功能
  muLu_obj: any = [];
  muLu_objS: any = {};
  muLu_id: string;
  // 产品回显功能
  keshou_obj: any = {};
  keshou_objS: any = {};
  col_id: string;
  // 产品回显标签
  biaoq_obj: any = {};
  biaoq_objS: any = {};
  biaoq_id: string;
  ArrObj: any = {};
  iscompele: boolean = false;
  // 站点数据回显
  selectValue: any = {};
  customerListtemp: any; // 分类集合
  mulu: any;
  iscompeled: boolean = false;
  constructor(
    public routerR: Router,
    public router: ActivatedRoute,
    public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc, ) {
    this.codeValues(); // 调用方法，获取全部码值
    this.deliveryType = this.code['DeliveryTypes'];
  }

  ngOnInit() {
    this.deliveryTypeV = 'Y';
    let time21 = new Date();
    let time212 = new Date(time21.getTime() + 24 * 60 * 60 * 1000);
    this.transferedDateNow = time212;
    let channelIdlist = [];
    this.router.params.subscribe((data) => {
      channelIdlist = data['channelId'].split(',');
      this.siteNameList = [];
      channelIdlist.forEach(item => {
        this.channelId = item;
        let parm = {
          channelId: this.channelId,
        };
        this.productManageHttpService.queryProdCataListForChannelPage(parm).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.siteNameList.push(data.prodChannelList[0]);
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
      });
    });
  }
  ngOnChanges() {
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  // 点击站点
  siteClick(col) {
    this.issiteFClick = true;
    this.setSelectValuess();
    this.siteId = col.channelId;
    this.showClassSite = col.channelName;
    this.productStoreId = col.productStoreId;
    this.muluQuery();
    this.isSiteClick = true;
  }
  // 目录默认查询
  muluQuery() {
    let parms = { productStoreId: this.productStoreId };
    this.productManageHttpService.queryCatalogListFromProdStore(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.muluName = data.prodCatalogList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 目录变更
  shelveMain() {
    this.display = true;
    this.headerTitle = '店面商家目录维护';
    this.showModel = '2';
    if (this.muluName.length == 0) {
      this.mulu = 1;
    } else {
      this.mulu = 2;
    }
    this.updateValue = {
      productStoreId: this.productStoreId,
      storeName: this.storeName,
      channelId: this.channelId,
      mulu: this.mulu,
    };
  }
  // 目录变更回调
  mainCall(param) {
    this.display = param;
    this.muluQuery();
  }
  // 已上架目录
  muluClick(col) {
    this.ctlgIdValue = col.prodCatalogId;
    let parm = { ctlgId: col.prodCatalogId, pageSize: 10000, flag: '0' };
    this.productManageHttpService.ctlgMenuQuery(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.fenlei_id = '';
        this.showClass = '';
        this.all_tree = data.customerMenuList;
        this.left_tree_arr = data.customerMenuList;
        this.left_tree = this.hierarchyTree(data.customerMenuList, '');
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 树
  nodeCheck(event) {
    // 设置值
    this.gongneng_list = []; // 功能列表
    this.label_list = []; // 标签列表
    this.fenlei_id = event.node.value[0];
    // 设置可售产品的回显信息设置站点
    if (!this.selectValue[this.siteId]) {
      this.selectValue[this.siteId] = {};
    }
    // 设置目录下的分类回显
    if (!this.selectValue[this.siteId][this.fenlei_id]) {
      this.selectValue[this.siteId][this.fenlei_id] = {};
    }
    // 设置可售产品的回显
    if (!this.selectValue[this.siteId][this.fenlei_id]['productCheckbox']) {
      this.selectValue[this.siteId][this.fenlei_id]['productCheckbox'] = [];
    }
    let this_ = this;
    this.hierarchy_arr = this.getMeuInfo(this.left_tree_arr, event.node.value[0])
    let parames = {
      pageNum: 1,
      pageSize: 10000,
      ctlgId: event.node.value[0],
      maxIdLevel: this.hierarchy_arr[1] + '',
      ctlgIdLevel: this.hierarchy_arr[0] + ''
    };

    const delData = (data) => {
      this.keshou_list = [];
      this.customerListtemp = data.customerListtemp;
      data.customerListtemp.forEach(item => {
        this.keshou_list.push({
          name: item.productName,
          id: item.productId
        });
      });
      // 设置回显的值
      this.productCheckbox = [];
      this.productCheckbox = this.selectValue[this.siteId][this.fenlei_id]['productCheckbox'];
    };
    this.interfaceComFnc(this.productManageHttpService.ctlgManageContactAttrInfo(parames), delData);
    // 当选站点下主目录
  }
  // +号点击
  nodeExpend(event) {
    event.node.children = this.hierarchyTree(this.all_tree, event.node.value[0]);
  }

  interfaceComFnc(service_method, success_fnc) {
    service_method.subscribe(data => {
      if (data.returnCode != null && data.returnCode.code == "success") {
        if (success_fnc !== null) {
          success_fnc(data);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      });
  }
  // 层级树
  hierarchyTree(menu_list, pid) {
    let menu_arr = [];
    for (let key of menu_list) {
      if (key.pid === pid) {
        menu_arr.push({
          children: [{}],
          label: key.name,
          value: [key.id, key.pid]
        });
      }
    }
    return menu_arr;
  }
  getMeuInfo(tree, curren_pid) {// 一级菜单  主目录id
    let count = 1;
    let getFirstId = (tree) => {
      let id = '';
      tree.every(item => {
        item.pid == '' ? id = item.id : null;
      });
      return id;
    };
    // 末级菜单id
    let getLastId = (tree) => {
      let id_arr = [];
      tree.forEach(item => {
        let stu = tree.every(item2 => {
          if (item.id != item2.pid) {
            return true;
          }
        }) ? id_arr.push(item.id) : null;
      });
      return id_arr;
    };
    // 最大层级深度
    let getMaxHierarchy = (tree, first_id, last_arr) => {
      let arr = [];
      last_arr.forEach(item => {
        count = 1;
        let max = currentHierarchy(tree, first_id, item);
        arr.push(max);
      });
      return arr.sort()[arr.length - 1];
    };
    // 当前层级深度
    // 记录层级次数
    function currentHierarchy(tree, first_id, pid) {
      if (pid != first_id) {
        tree.some(item => {
          if (item.id === pid) {
            count++;
            currentHierarchy(tree, first_id, item.pid);
            return false;
          }
        });

      }
      return count;
    }

    let first_id = getFirstId(tree); // 一级菜单id
    let last_arr = getLastId(tree); // 末级菜单 id组
    let current_hierarchy = currentHierarchy(tree, first_id, curren_pid); // 当前层级
    let max_hierarchy = getMaxHierarchy(tree, first_id, last_arr); // 最大层级
    return [current_hierarchy, max_hierarchy];
  }


  // 产品点击
  productClick(col) {
    // 设置值
    this.setSelectValuess();
    this.xuanze = [];
    this.bqxuanze = [];
    this.showClass = col.id;
    this.ArrObj = col;
    // 设置目录分类的值
    // 设置可售产品的回显的值
    if (!this.selectValue[this.siteId][this.fenlei_id][this.showClass]) {
      this.selectValue[this.siteId][this.fenlei_id][this.showClass] = {};
    }
    // 产品投放回显值
    if (!this.selectValue[this.siteId][this.fenlei_id][this.showClass]['xuanze']) {
      this.selectValue[this.siteId][this.fenlei_id][this.showClass]['xuanze'] = [];
    }
    // 标签回显
    if (!this.selectValue[this.siteId][this.fenlei_id][this.showClass]['bqxuanze']) {
      this.selectValue[this.siteId][this.fenlei_id][this.showClass]['bqxuanze'] = [];
    }
    this.funQuery(this.showClass);
    this.labelQuery(this.showClass);
  }


  // 标签查询
  labelQuery(dataA) {
    this.label_list = [
      { labelName: '推荐', labelId: '推荐' },
      { labelName: '热销', labelId: '热销' },
    ];
    // 查询执行一次回显
    this.toufanglistA.forEach(item => {
      if (item.siteId == this.siteId && item.categoryId == this.fenlei_id) {
        item.list.forEach(itemID => {
          if (dataA == itemID.productId && itemID.tabindex === 'one') {
            itemID.productLabelList.forEach(funt => {
              if (funt.state == '25') {
                this.bqxuanze.push(funt.productLableName);
              }
            });
            itemID.tabindex = 'two';
            this.setSelectValuess();
          }
        });
      }
    });
    // 设置标签的回显值
    this.bqxuanze = this.selectValue[this.siteId][this.fenlei_id][this.showClass]['bqxuanze'];
  }
  // 功能查询
  funQuery(data) {
    let parmFun = { productId: data };
    this.productManageHttpService.getProductFunctionByProductId(parmFun).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.gongneng_list = data.functionList;
        // 查询执行一次回显
        this.toufanglistA.forEach(item => {
          if (item.siteId == this.siteId && item.categoryId == this.fenlei_id) {
            item.list.forEach(itemID => {
              if (parmFun.productId == itemID.productId && itemID.funindex === 'one') {
                itemID.productFuntionList.forEach(funt => {
                  if (funt.state == '25') {
                    this.xuanze.push(funt.productFunctionId);
                  }
                });
                itemID.funindex = 'two';
                this.setSelectValuess();
              }
            });
          }
        });
        // 设置功能的回显值
        this.xuanze = this.selectValue[this.siteId][this.fenlei_id][this.showClass]['xuanze'];
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  changeDeliveryV() {
    if (this.deliveryTypeV == 'Y') {
      this.dataV = true;
    } else {
      this.dataV = false;
    }
  }
  // 最后投放
  toufangServe() {
    this.iscompeled = true;
    this.iscompele = true;
    if (this.deliveryTypeV === 'N' && this.startTime === null) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择投放日期' });
      this.iscompele = false;
      this.iscompeled = false;
      return;
    }
    // this.productClick(this.ArrObj);
    let Arr1 = [];
    let arrAllV = [];
    for (let key1 in this.selectValue) {
      for (let key2 in this.selectValue[key1]) {
        if (this.selectValue[key1][key2].productCheckbox.length > 0) {
          this.selectValue[key1][key2].productCheckbox.forEach(item1 => {
            let objAll = {
              functionList: [],
              labelList: [],
              productId: '',
              launchTime: null,
              productCategoryId: '',
              channelId: '',
            };
            let Arr10 = [];
            let Arr11 = [];
            this.selectValue[key1][key2][item1].xuanze.forEach(item2 => {
              let obj = {};
              obj['productFunctionId'] = item2;
              obj['excDesc'] = '';
              Arr10.push(obj)
            })
            this.selectValue[key1][key2][item1].bqxuanze.forEach(item3 => {
              let obj = {};
              obj['labelName'] = item3;
              obj['labelLevel'] = '';
              Arr11.push(obj)
            })
            objAll.functionList = Arr10;
            objAll.labelList = Arr11;
            objAll.productId = item1;
            objAll.channelId = key1;
            let Arr12 = '';
            this.customerListtemp.forEach(item3 => {
              if (item3.productId === item1) {
                Arr12 = item3.productCategoryId;
              }
            });
            objAll.productCategoryId = Arr12;
            if (this.deliveryTypeV == 'Y') {
              objAll.launchTime = null;
            } else {
              objAll.launchTime = this.commfunc.transDate(this.startTime);
            }
            arrAllV.push(objAll)
          });
        }
      }
    }
    let upperFrameTimes = '';
    if (this.deliveryTypeV == 'Y') {
      upperFrameTimes = '';
    } else {
      upperFrameTimes = this.commfunc.transDate(this.startTime);
    }
    if (arrAllV.length <= 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择投放产品' });
      this.iscompeled = false;
      return;
    }
    let praM = {
      channeAndProductList: arrAllV,
      upperFrameFlag: this.deliveryTypeV, // 投放标识
      upperFrameTime: upperFrameTimes, // 投放时间
    };
    this.productManageHttpService.productUpperFrameTzb(praM).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        let this_ = this;
        setTimeout(function () {
          this_.routerR.navigate(['/pages/tzb/product-manage/product-market/product-launch/channel-delivery'])
          this.iscompele = false;
        }, 2000);
      } else {
        this.iscompeled = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        this.iscompele = false;
      }
    }, error => {
      this.iscompeled = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      this.iscompele = false;
    });
  }

  // 设置值的方法
  setSelectValuess() {
    if (this.fenlei_id != '-1' && this.siteId) {
      // 设置可售产品
      if (!this.selectValue[this.siteId]) {
        this.selectValue[this.siteId] = {};
      }
      if (!this.selectValue[this.siteId][this.fenlei_id]) {
        this.selectValue[this.siteId][this.fenlei_id] = {};
      }
      if (!this.selectValue[this.siteId][this.fenlei_id]['productCheckbox']) {
        this.selectValue[this.siteId][this.fenlei_id]['productCheckbox'] = [];
      }
      let aa = _.clone(this.productCheckbox);
      this.selectValue[this.siteId][this.fenlei_id]['productCheckbox'] = aa;
      if (this.siteId && this.fenlei_id && this.showClass) {
        // 产品投放回显值
        if (!this.selectValue[this.siteId][this.fenlei_id][this.showClass]) {
          this.selectValue[this.siteId][this.fenlei_id][this.showClass] = {};
        }
        if (!this.selectValue[this.siteId][this.fenlei_id][this.showClass]['xuanze']) {
          this.selectValue[this.siteId][this.fenlei_id][this.showClass]['xuanze'] = [];
        }
        let bb = _.clone(this.xuanze);
        this.selectValue[this.siteId][this.fenlei_id][this.showClass]['xuanze'] = bb;
        // 标签选着
        if (!this.selectValue[this.siteId][this.fenlei_id][this.showClass]['bqxuanze']) {
          this.selectValue[this.siteId][this.fenlei_id][this.showClass]['bqxuanze'] = [];
        }
        let cc = _.clone(this.bqxuanze);
        this.selectValue[this.siteId][this.fenlei_id][this.showClass]['bqxuanze'] = cc;
      }
    }
    if (this.issiteFClick) {
      this.left_tree = [];
      this.all_tree = [];
      this.keshou_list = [];
      this.gongneng_list = [];
      this.label_list = [];
      // this.xuanze = [];
      // this.bqxuanze = [];
      this.issiteFClick = false;
    }
  }
  // 按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id);
  }
  goBack() {
    window.history.go(-1);
  }
}
