//投放查询 熊正好 2019.4
import { Component, OnInit, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ChannelDeliveryBean, ChannelDeliveryBeanO, ChannelDeliveryBeanT } from './bean/channel-query.bean';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ActivatedRoute, Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-delivery-query',
  templateUrl: './delivery-query.component.html',
  styleUrls: ['./delivery-query.component.scss'],
  providers: [ProductManageHttpService]
})
/**
 * 投放查询
 */
export class DeliveryQueryComponent implements OnInit, OnChanges {
  tabMesg: any = tableMessage;//列表无数据
  // bean
  channelDeliveryBean1: ChannelDeliveryBean = new ChannelDeliveryBean();
  channelDeliveryBean2: ChannelDeliveryBeanO = new ChannelDeliveryBeanO();
  channelDeliveryBean3: ChannelDeliveryBeanT = new ChannelDeliveryBeanT();
  selectedValue: string = 'val1';//选择方式
  site_list1: any = [];//站点列表
  site_list2: any = [];//站点列表
  site_list3: any = [];//站点列表
  select_atr1: any = [];
  select_atr2: any = [];
  select_atr3: any = [];
  star1: any = {};//URL传参
  star2: any = {};//URL传参
  star3: any = {};//URL传参
  //总页数
  total: string;
  total2: string;
  total3: string;
  first1: number = 0;//分页控制
  first2: number = 0;//分页控制
  first3: number = 0;//分页控制
  //提示信息
  deliveryQueryType: any;
  code: any;
  msgs: Message[] = [];
  //批次号
  applySerialNo: string = '';//批次号
  deliveryQueryTypeV: string = '';//投放下架标志
  constructor(public productManageHttpService: ProductManageHttpService,
    public router: Router,
    public commfunc: Commfunc) {
    this.codeValues(); // 调用方法，获取全部码值
    this.deliveryQueryType = this.code['DeliveryQueryType'];
    // this.deliveryQueryType = [
    //   { value: '', label: '请选择' },
    //   // { value: '23', label: '新增待上架' },
    //   // { value: '24', label: '修改待上架' },
    //   // { value: '25', label: '已上架' },
    //   // { value: '26', label: '已下架' },
    //   // { value: '27', label: '待下架' },
    //   // { value: '28', label: '下架后重上架' },
    //   { value: 'ON', label: '投放' },
    //   { value: 'OFF', label: '下架' },
    // ];
  }
  ngOnInit() {
    this.queryClick1();
    this.queryClick2();
    this.queryClick3();
  }
  ngOnChanges() {
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  chooseValue() {
    if (this.selectedValue === 'val1') {
      this.channelDeliveryBean1.pageNum = 1;
      this.channelDeliveryBean2.pageNum = 1;
      this.channelDeliveryBean3.pageNum = 1;
    }
    if (this.selectedValue === 'val2') {
      this.channelDeliveryBean1.pageNum = 1;
      this.channelDeliveryBean2.pageNum = 1;
      this.channelDeliveryBean3.pageNum = 1;
    }
    if (this.selectedValue === 'val3') {
      this.channelDeliveryBean1.pageNum = 1;
      this.channelDeliveryBean2.pageNum = 1;
      this.channelDeliveryBean3.pageNum = 1;
    }
  }


  // 重置
  resetClick1() {
    this.channelDeliveryBean1 = new ChannelDeliveryBean()
  }
  // 重置
  resetClick2() {
    this.channelDeliveryBean2 = new ChannelDeliveryBeanO()
  }
  // 重置
  resetClick3() {
    this.channelDeliveryBean3 = new ChannelDeliveryBeanT()
  }
  // 查询始终第一页
  querySecle1() {
    this.channelDeliveryBean1.pageSize = 10;
    this.channelDeliveryBean1.pageNum = 1;
    this.first1 = 0;
    this.queryClick1();
  }
  // 查询
  queryClick1() {
    this.productManageHttpService.productChannelList(this.channelDeliveryBean1).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.site_list1 = data.productChannelList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 查询始终第一页
  querySecle2() {
    this.channelDeliveryBean2.pageSize = 10;
    this.channelDeliveryBean2.pageNum = 1;
    this.first2 = 0;
    this.queryClick2();
  }
  // 查询
  queryClick2() {
    this.productManageHttpService.queryApplySerialNo(this.channelDeliveryBean2).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.site_list2 = data.launchInfo;
        this.total2 = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //查询始终第一页
  querySecle3() {
    this.channelDeliveryBean3.pageSize = 10;
    this.channelDeliveryBean3.pageNum = 1;
    this.first3 = 0;
    this.queryClick3();
  }
  //查询
  queryClick3() {
    this.productManageHttpService.findAvailableProductList(this.channelDeliveryBean3).subscribe(data => {//availableAndProductPackageQuery
      if (data.returnCode.code == "success") {
        this.site_list3 = data.resultList;//productList
        this.total3 = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //分页
  paginate1(event) {
    // this.select_atr1 = [];
    //每页显示的条数
    this.channelDeliveryBean1.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.channelDeliveryBean1.pageNum = currentPage;
    this.first1 = (currentPage - 1) * this.channelDeliveryBean1.pageSize;
    //调用查询的方法
    this.queryClick1();
    this.channelDeliveryBean1.pageNum = 1;
  }
  //分页
  paginate2(event) {
    // this.select_atr2 = [];
    //每页显示的条数
    this.channelDeliveryBean2.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.channelDeliveryBean2.pageNum = currentPage;
    this.first2 = (currentPage - 1) * this.channelDeliveryBean2.pageSize;
    //调用查询的方法
    this.queryClick2();
    this.channelDeliveryBean2.pageNum = 1;
  }
  //分页
  paginate3(event) {
    // this.select_atr3 = [];
    //每页显示的条数
    this.channelDeliveryBean3.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.channelDeliveryBean3.pageNum = currentPage;
    this.first3 = (currentPage - 1) * this.channelDeliveryBean3.pageSize;
    //调用查询的方法
    this.queryClick3();
    this.channelDeliveryBean3.pageNum = 1;
  }
  launchQuery1() {
    if (this.select_atr1.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "请选择查询的站点" });
    } else {
      this.select_atr1.forEach((i) => {
        if (this.star1['channelId']) {
          this.star1['channelId'].push(i);
        } else {
          this.star1['channelId'] = [];
          this.star1['channelId'].push(i);
        }
      });
      this.router.navigate(['/pages/tzb/product-manage/product-market/product-launch/site-query-details', this.star1])
    }
  }
  launchQuery2() {
    if (this.select_atr2.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "请选择查询的批次号" });
    } else {
      this.select_atr2.forEach((i) => {
        if (this.star2['applySerialNo']) {
          this.star2['applySerialNo'].push(i);
        } else {
          this.star2['applySerialNo'] = []
          this.star2['applySerialNo'].push(i);
        }
      });
      this.router.navigate(['/pages/tzb/product-manage/product-market/product-launch/batch-query-details', this.star2])
    }

  }
  launchQuery3() {
    if (this.select_atr3.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "请选择查询的产品或套餐" });
    } else {
      this.select_atr3.forEach((i) => {
        this.site_list3.forEach(item => {
          if (item.productId == i) {
            if (this.star3['productId']) {
              this.star3['productId'].push({
                productId: item.productId,
                productName: item.productName,
              });
            } else {
              this.star3['productId'] = [];
              this.star3['productId'].push({
                productId: item.productId,
                productName: item.productName,
              });
            }
          }
        });
      });
      this.star3['productId'] = JSON.stringify(this.star3['productId']);
      this.router.navigate(['/pages/tzb/product-manage/product-market/product-launch/pro-query-details', this.star3])
    }

  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
