import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductManageHttpService } from "./../../../../http/product-manage.http.service";
import { SelectItem, Message } from 'primeng/primeng';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-site-query-details',
  templateUrl: './site-query-details.component.html',
  styleUrls: ['./site-query-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class SiteQueryDetailsComponent implements OnInit {
  siteIdList: any = []; // 站点ID数组
  siteId: string; // 站点ID
  siteNameList: any = []; // 站点Name数组
  msgs: Message[] = []; // 提示信息
  storeName: string; // 店铺名称
  productStoreId: string; // 店铺标识
  muluName: any; // 目录
  showClass: string; // 点击组件
  showClassProduct: string; // 点击产品
  showClasslog: string; // 点击目录
  muluFen: any; // 目录下架分类
  funList: any = []; // 产品功能投放
  labelList: any = []; // 标签选择
  selectedFiles: TreeNode[];

  // 树结构
  treeData: any;
  left_tree: any = [];
  left_tree_arr: any = [];
  all_tree; any = [];
  hierarchy_arr: any = [];
  ctlgIdValue: any;

  productCheckbox: any = [];
  keshou_list: any; // 可售列表
  // 管道：产品状态
  productPackType: any; // 套餐状态
  productDeliveryType: any; // 产品状态
  code: any;

  constructor(public router: ActivatedRoute,
    public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc, ) {
    this.codeValues(); // 调用方法，获取全部码值
    this.productPackType = this.code['ProductPackType'];
    this.productDeliveryType = this.code['ProductDeliveryType'];
  }

  ngOnInit() {
    // this.productDeliveryType = [
    //   { value: '23', label: '新增待上架' },
    //   { value: '24', label: '修改待上架' },
    //   { value: '25', label: '已上架' },
    //   { value: '26', label: '已下架' },
    //   { value: '27', label: '待下架' },
    //   { value: '28', label: '下架后重上架' },
    //   { value: '32', label: '投放异常' },
    //   { value: '33', label: '下架异常' },
    //   { value: '99', label: '上架否决' },
    //   { value: '98', label: '下架否决' },
    //   { value: 'ON', label: '已上架' },
    //   { value: 'OFF', label: '已下架' },
    // ];
    this.router.params.subscribe((data) => {
      this.siteIdList = data['channelId'].split(",");
    });
    this.siteIdList.forEach(siteID => {
      let parm = { channelId: siteID };
      this.productManageHttpService.queryProdCataListForChannelPage(parm).subscribe(data => {
        if (data.returnCode.code == "success") {
          data.prodChannelList.forEach(item => {
            this.siteNameList.push({
              channelId: siteID,
              channelName: item.channelName,
              productStoreId: item.productStoreId,
              storeName: item.storeName,
            });
          });
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      });
    });
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  // 点击站点
  siteClick(col) {
    this.siteId = col.channelId;
    this.showClass = col.channelName;
    this.productStoreId = col.productStoreId;
    this.storeQuery(col.productStoreId);
    this.left_tree = [];
    this.keshou_list = [];
    this.funList = [];
    this.labelList = [];
  }
  // 店面默认查询
  storeQuery(item) {
    let parmss = { productStoreId: item };
    this.productManageHttpService.productStoreDetail(parmss).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.storeName = data.storeName;
        this.muluQuery();
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 目录默认查询
  muluQuery() {
    let parms = { productStoreId: this.productStoreId }
    this.productManageHttpService.queryCatalogListFromProdStore(parms).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.muluName = data.prodCatalogList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 已上架目录
  muluClick(col) {
    this.showClasslog = col.catalogName;
    this.ctlgIdValue = col.prodCatalogId;
    let parm = { ctlgId: col.prodCatalogId, pageSize: 10000 };
    this.productManageHttpService.ctlgMenuQuery(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.all_tree = data.customerMenuList;
        this.left_tree_arr = data.customerMenuList;
        this.left_tree = this.hierarchyTree(data.customerMenuList, '');
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  nodeCheck(event) {
    let this_ = this;
    this.hierarchy_arr = this.getMeuInfo(this.left_tree_arr, event.node.value[0]);
    let parames = {
      ctlgId: event.node.value[0],
      maxIdLevel: this.hierarchy_arr[1],
      ctlgIdLevel: this.hierarchy_arr[0]
    };
    let paramesQuery = {
      channelId: this.siteId,
      prodCategoryId: event.node.value[0]
    };
    this.muluFen = parames;
    let parames2 = {
      ctlgId: event.node.value[0],
    };

    let delData = (data) => {
      this.keshou_list = [];
      this.funList = [];
      this.labelList = [];
      this.keshou_list = data.productList;
    };
    this.interfaceComFnc(this.productManageHttpService.websiteProductStateQuery(paramesQuery), delData);
  }
  // +号点击
  nodeExpend(event) {
    event.node.children = this.hierarchyTree(this.all_tree, event.node.value[0]);
  }

  interfaceComFnc(service_method, success_fnc) {
    service_method.subscribe(data => {
      if (data.returnCode != null && data.returnCode.code == "success") {
        if (success_fnc !== null) {
          success_fnc(data);
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      });
  }
  // 层级树
  hierarchyTree(menu_list, pid) {
    let menu_arr = [];
    for (let key of menu_list) {
      if (key.pid === pid) {
        menu_arr.push({
          children: [{}],
          label: key.name,
          value: [key.id, key.pid]
        });
      }
    }
    return menu_arr;
  }
  getMeuInfo(tree, curren_pid) {// 一级菜单  主目录id
    let count = 1;
    let getFirstId = (tree) => {
      let id = '';
      tree.every(item => {
        item.pid == '' ? id = item.id : null;
      });
      return id;
    };
    // 末级菜单id
    let getLastId = (tree) => {
      let id_arr = [];
      tree.forEach(item => {
        let stu = tree.every(item2 => {
          if (item.id != item2.pid) {
            return true;
          }
        }) ? id_arr.push(item.id) : null;
      });
      return id_arr;
    };
    // 最大层级深度
    let getMaxHierarchy = (tree, first_id, last_arr) => {
      let arr = [];
      last_arr.forEach(item => {
        count = 1;
        let max = currentHierarchy(tree, first_id, item)
        arr.push(max);
      });
      return arr.sort()[arr.length - 1];
    };
    //当前层级深度
    //记录层级次数
    function currentHierarchy(tree, first_id, pid) {
      if (pid != first_id) {
        tree.some(item => {
          if (item.id === pid) {
            count++;
            currentHierarchy(tree, first_id, item.pid);
            return false;
          }
        });

      }
      return count;
    }

    let first_id = getFirstId(tree); // 一级菜单id
    let last_arr = getLastId(tree); // 末级菜单 id组
    let current_hierarchy = currentHierarchy(tree, first_id, curren_pid); // 当前层级
    let max_hierarchy = getMaxHierarchy(tree, first_id, last_arr); // 最大层级 
    return [current_hierarchy, max_hierarchy];
  }
  // 产品查功能标签
  productQueryF(col) {
    this.showClassProduct = col.productName;
    this.funList = [];
    this.labelList = [];
    this.funList = col.functionList;
    this.labelList = col.labelList;
  }
  goBack() {
    window.history.go(-1);
  }
}
