// 渠道投放-查询 熊正好 2019.4
import { Component, OnInit } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ChannelDeliveryBean } from './bean/channel-delivery.bean';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ActivatedRoute, Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
@Component({
  selector: 'app-channel-delivery',
  templateUrl: './channel-delivery.component.html',
  styleUrls: ['./channel-delivery.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class ChannelDeliveryComponent implements OnInit {

  tabMesg: any = tableMessage; // 列表无数据
  // bean
  channelDeliveryBean: ChannelDeliveryBean = new ChannelDeliveryBean();
  all_title: string = '渠道投放';
  //   ceshi:any="ceswsds"
  select_1: SelectItem[] = []; // 默认下拉
  star: any = {}; // URL传参
  cars_2: any;
  // 列表
  site_list: any = [];

  // 提示信息
  msgs: Message[] = [];
  // 总页数
  total: string;
  first: number = 0; // 分页控制
  // 模态框
  showModel: string;
  headerTitle: string;
  display: boolean = false;
  select_atr: any = []; // 所选站点
  select_business: any = []; // 店铺下拉

  arrA: any = []; // 所选站点ID
  dataS: string; // 所选站点ID

  constructor(public productManageHttpService: ProductManageHttpService, public router: Router,
    public commfunc: Commfunc) {
    this.select_business = [
      { label: '请选择', value: "" },
    ];
  }


  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    // this.business_query();
    this.queryClick();
  }

  // 重置
  resetClick() {
    this.channelDeliveryBean = new ChannelDeliveryBean();
  }


  // 店铺下拉
  business_query() {
    let objDate = {
      pageNum: 1,
      pageSize: 1000,
    };
    this.productManageHttpService.productStoreList(objDate).subscribe(data => {
      if (data.returnCode.code == "success") {
        data.productStoreList.forEach(item => {
          let obj = {};
          obj['label'] = item.storeName;
          obj['value'] = item.productStoreId;
          this.select_business.push(obj);
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  // 查询始终第一页
  querySecle() {
    this.channelDeliveryBean.pageSize = 10;
    this.channelDeliveryBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  // 查询
  queryClick() {
    this.channelDeliveryBean.allFlag = '0';
    this.productManageHttpService.productChannelList(this.channelDeliveryBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.site_list = data.productChannelList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 投放
  deliveryClick() {
    if (this.select_atr.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "请选择所投放的渠道" });
    } else {
      this.select_atr.forEach((i) => {
        if (this.star['channelId']) {
          this.star['channelId'].push(i);
        } else {
          this.star['channelId'] = [];
          this.star['channelId'].push(i);
        }
      });
      this.router.navigate(['/pages/tzb/product-manage/product-market/product-launch/channel-delivery-details', this.star]);
    }
  }
  // 下架
  xiajia() {
    if (this.select_atr.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "请选择所下架的渠道" });
    } else {
      this.select_atr.forEach((i) => {
        if (this.star['channelId']) {
          this.star['channelId'].push(i);
        } else {
          this.star['channelId'] = [];
          this.star['channelId'].push(i);
        }
      });
      this.router.navigate(['/pages/tzb/product-manage/product-market/product-launch/product-lower-frame', this.star]);
    }
  }
  // 分页
  paginate(event) {
    // 每页显示的条数
    this.channelDeliveryBean.pageSize = event.rows * 1;
    // 当前页
    let currentPage = event.page + 1;
    this.channelDeliveryBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.channelDeliveryBean.pageSize;
    // 调用查询的方法
    this.queryClick();
    this.channelDeliveryBean.pageNum = 1;
  }
  // 按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id);
  }
}
