import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductManageHttpService } from "./../../../../http/product-manage.http.service";
import { SelectItem, Message } from 'primeng/primeng';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'app-pro-query-details',
  templateUrl: './pro-query-details.component.html',
  styleUrls: ['./pro-query-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class ProQueryDetailsComponent implements OnInit {
  batchNum: string; // 所属批次号
  msgs: Message[] = []; // 提示信息
  showClass: string; // 点击产品//暂存产品ID
  showClassSite: string; // 点击站点
  productList: any = []; // 入参产品ID
  productListN: any = []; // 产品列表
  siteList: any = []; // 站点列表
  functionList: any = []; // 功能列表
  labelList: any = []; // 标签列表
  productDeliveryType: any; // 投放状态
  code: any;
  constructor(public productManageHttpService: ProductManageHttpService,
    public router: ActivatedRoute,
    public commfunc: Commfunc) {
    this.codeValues(); // 调用方法，获取全部码值
    this.productDeliveryType = this.code['ProductDeliveryType'];
  }

  ngOnInit() {
    // this.productDeliveryType = [
    //   { value: '23', label: '新增待上架' },
    //   { value: '24', label: '修改待上架' },
    //   { value: '25', label: '已上架' },
    //   { value: '26', label: '已下架' },
    //   { value: '27', label: '待下架' },
    //   { value: '28', label: '下架后重上架' },
    //   { value: '32', label: '投放异常' },
    //   { value: '33', label: '下架异常' },
    //   { value: '99', label: '上架否决' },
    //   { value: '98', label: '下架否决' },
    //   { value: 'ON', label: '已上架' },
    //   { value: 'OFF', label: '已下架' },
    // ];
    this.router.params.subscribe((data) => {
      this.productList = JSON.parse(data['productId']);
    });
    this.productListN = this.productList;
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  // 点击产品
  productClick(col) {
    this.siteList = [];
    this.functionList = [];
    this.labelList = [];
    this.batchNum = '';
    this.showClass = col.productId;
    let parm = {
      productId: col.productId,
      channelId: '',
    };
    this.productManageHttpService.queryByProductId(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.siteList = data.launchInfo.channelList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 点击站点
  siteClick(col) {
    this.functionList = [];
    this.labelList = [];
    this.showClassSite = col.channelId;
    let parm = {
      channelId: col.channelId,
      productId: this.showClass,
    };
    this.productManageHttpService.queryByProductId(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.functionList = data.launchInfo.functionList;
        this.labelList = data.launchInfo.lableList;
        this.batchNum = data.launchInfo.applySerialNo;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  goBack() {
    window.history.go(-1);
  }
}
