import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SelectItem, Message } from 'primeng/primeng';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { ProductManageHttpService } from '../../../../http/product-manage.http.service';
import * as _ from 'lodash';
import { Input } from '@angular/core';

@Component({
  selector: 'app-batch-query-details',
  templateUrl: './batch-query-details.component.html',
  styleUrls: ['./batch-query-details.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
export class BatchQueryDetailsComponent implements OnInit {
  @Input() inValue: any;
  Gzldis: boolean = false;
  cattlehide: boolean = true;
  msgs: Message[] = [];
  showClass: string; // 点击批次号
  showClassSite: string; // 点击站点
  showClassProduct: string; // 点击产品
  batchdList: any = [];
  batchdListV: any = [];
  siteList: any = []; // 站点列表
  selectVlue: any = []; // 所选站点
  productListN: any = []; // 产品列表
  functionList: any = []; // 功能列表
  labelList: any = []; // 标签列表
  parmLz: any;
  productDeliveryType: any; // 投放状态
  code: any;
  constructor(public router: ActivatedRoute,
    public productManageHttpService: ProductManageHttpService,
    public commfunc: Commfunc, ) {
    this.codeValues(); // 调用方法，获取全部码值
    this.productDeliveryType = this.code['ProductDeliveryType'];
  }

  ngOnInit() {
    // this.productDeliveryType = [
    //   { value: '23', label: '新增待上架' },
    //   { value: '24', label: '修改待上架' },
    //   { value: '25', label: '已上架' },
    //   { value: '26', label: '已下架' },
    //   { value: '27', label: '待下架' },
    //   { value: '28', label: '下架后重上架' },
    //   { value: '32', label: '投放异常' },
    //   { value: '33', label: '下架异常' },
    //   { value: '99', label: '上架否决' },
    //   { value: '98', label: '下架否决' },
    //   { value: 'ON', label: '已上架' },
    //   { value: 'OFF', label: '已下架' },
    // ];
    if (!this.inValue) {
      if (this.router.snapshot.params['applySerialNo'] && this.router.snapshot.params['status']) {
        this.Gzldis = true;
        this.parmLz = {
          applySerialNo: this.router.snapshot.params['applySerialNo'],
          channelId: '',
          productId: '',
          // launchDate:this.router.snapshot.params['launchDate'],
          status: this.router.snapshot.params['status']
        };
      }
      this.router.params.subscribe((data) => {
        this.batchdList = data['applySerialNo'].split(",");
      });
    } else {
      console.log(this.inValue);
      this.Gzldis = true;
      this.cattlehide = false;
      this.parmLz = {
        applySerialNo: this.inValue.applySerialNo,
      };
      this.batchdList.push(this.inValue.applySerialNo);
    }
    this.batchdList.forEach(item => {
      this.batchdListV.push({
        applySerialNo: item,
      });
    });
  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }
  // 点击批次号
  batchClick(col) {
    this.siteList = [];
    this.productListN = [];
    this.labelList = [];
    this.functionList = [];
    this.showClass = col.applySerialNo;
    this.parmLz = {
      applySerialNo: col.applySerialNo,
      channelId: '',
      productId: ''
    };
    this.productManageHttpService.queryByApplySerialNo(this.parmLz).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.siteList = data.launchInfo.channelList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 点击站点
  siteClick(col) {
    this.productListN = [];
    this.labelList = [];
    this.functionList = [];
    this.showClassSite = col.channelId;
    let parm = {
      applySerialNo: this.showClass,
      channelId: col.channelId,
      productId: ''
    };
    this.productManageHttpService.queryByApplySerialNo(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.productListN = data.launchInfo.productList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  // 点击产品
  productClick(col) {
    this.showClassProduct = col.productId;
    let parm = {
      applySerialNo: this.showClass,
      channelId: this.showClassSite,
      productId: col.productId
    };
    this.productManageHttpService.queryByApplySerialNo(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.functionList = data.launchInfo.functionList;
        this.labelList = data.launchInfo.lableList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  notice() {
    this.Gzldis = true;
    if (this.selectVlue.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择需通知的站点' });
      this.Gzldis = false;
      return;
    }
    let arr = [];
    this.selectVlue.forEach(item => {
      arr.push(this.siteList[item].channelId);
    });
    arr = _.clone(arr);
    let parm = {
      applySerialNo: this.showClass,
      websiteIds: arr
    };
    this.productManageHttpService.reLaunch(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.Gzldis = false;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.Gzldis = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.Gzldis = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }
  goBack() {
    window.history.go(-1);
  }
}
