import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { Message, RadioButtonModule } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../../../http/product-manage.http.service";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config'//中文

@Component({
  selector: 'app-catalog-add',
  templateUrl: './catalog-add.component.html',
  styleUrls: ['./catalog-add.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 目录新增
 */
export class CatalogAddComponent implements OnInit {

  tabMesg: any = tableMessage;//列表无数据

  prodCatalogId: string;
  catalogName: string;

  list_catalog: any = [];
  valueListOne: any = [];
  //提示信息
  msgs: Message[] = [];

  //总页数
  total: string;
  //分页
  pageSize: any = 10;
  pageNum: any = 1;

  cars_2: any;
  deliveryType: any;//是否立即投放
  upperFrameFlag: string = '';//是否立即投放Value
  upperFrameTime: Date = null;
  code: any;
  dataV: boolean = true;
  transferedDateNow: Date = null;
  ch: any = CALENDAR_CN;

  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  list: any;
  constructor(public productManageHttpService: ProductManageHttpService, public commfunc: Commfunc, ) {
    this.codeValues() //调用方法，获取全部码值
    this.deliveryType = this.code['DeliveryTypes'];
  }

  ngOnInit() {
    this.upperFrameFlag = 'Y'
    let time21 = new Date();
    let time212 = new Date(time21.getTime() + 24 * 60 * 60 * 1000)
    this.transferedDateNow = time212;
    this.list = this.inValue
    this.queryClick()
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  changeDeliveryV() {
    if (this.upperFrameFlag == 'Y') {
      this.dataV = true;
    } else {
      this.dataV = false;
    }
  }

  // 目录新增默认查询
  queryClick() {
    let parm = {
      prodCatalogId: this.prodCatalogId,
      catalogName: this.catalogName,
      prodCatalogType: '1',
      pageSize: this.pageSize,
      pageNum: this.pageNum
    }
    this.productManageHttpService.catalogManageQueryList(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list_catalog = data.catalogManageInfoList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }
  mainSeve() {
    if (this.inValue.mulu == 2) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '渠道只允许关联一个目录，如果需要更换目录！' });
      return
    }
    if (this.inValue.productStoreId == undefined) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请先确定该站点下有关联店铺' });
      return
    }

    if (this.upperFrameFlag === 'N' && this.upperFrameTime === null) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择投放日期' });
      return;
    }

    let upperFrameTimes = '';
    if (this.upperFrameFlag == 'Y') {
      upperFrameTimes = '';
    } else {
      upperFrameTimes = this.commfunc.transDate(this.upperFrameTime);
    }

    let parm = {
      // productStoreId: this.inValue.productStoreId,
      channelId: this.inValue.channelId,
      prodCatalogId: this.valueListOne,
      upperFrameFlag: this.upperFrameFlag,
      upperFrameTime: upperFrameTimes,
    }

    this.productManageHttpService.pushCatalogOnProductStoreShelf(parm).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    //调用查询的方法
    this.queryClick();
    this.pageNum = 1;
  }

  goBack() {
    this.outValue.emit(false);
  }
  resetClick() {
    this.prodCatalogId = '';
    this.catalogName = '';
    this.queryClick();
  }
}
